.clickable {
  cursor: pointer !important;
}

.hoverable {

    .hover-show {
        display:none;
    }

    .hover-hide {
        display:inherit;
    }

    &:hover {
        .hover-show {
            display:inherit;
        }

        .hover-hide {
            display:none;
        }
    }

}

.no-border {
  border: none;
}
