

.btn {
box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.07);
    &:hover {
    box-shadow: 0px 2px 5px rgba(0, 0, 0,.2);
    }

    &:active {
     box-shadow: 0px 2px 1px rgba(0,0,0,.1), inset 0 2px 5px rgba(0,0,0,.05);
    }
}

@mixin button-variant($color, $background, $border) {
  color: $color;
  background-color: $background;
  border-color: $border;

  &:hover,
  &:focus,
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $color;
    background-color: lighten($background, 1%);
    border-color: $border;
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &:active,
    &.active {
      background-color:$background;
          border-color: $border;
    }
  }

  .badge {
    color: $background;
    background-color: $color;
  }
}

.btn-primary {
  @include button-variant(white, $brand-primary, $brand-primary);
}

.btn-default {
  @include button-variant($btn-default-color, $btn-default-bg, $btn-default-border);
}

.btn-clear-white {
  @include button-variant(inherit, rgba(0,0,0,0), rgba(0,0,0,0));
  box-shadow: none;
  &:hover {
  border:2px solid white;
  }
}

.btn-secondary {
  @include button-variant($btn-secondary-color, $btn-secondary-bg, $btn-secondary-border);
}


.modal-body.scrollable {
max-height: 500px;
height: 500px;
overflow-y: scroll;
box-shadow: inset 0px 0px 4px rgba(0,0,0,.1);

}

.modal-body.with-table {
padding: 0px;

.table th {
border-bottom: 1px solid #ddd;
}
.table > thead > tr > th:first-child, .table > tbody > tr > td:first-child {
padding-left: 20px;
}
.table > thead > tr > th:last-child, .table > tbody > tr > td:last-child {
padding-right: 20px;
}
}

.modal-footer {
text-align: left;
}


.table > tbody > tr > td {
vertical-align: middle;
}
.modal-title {
font-weight: 300;
text-transform: uppercase;
font-size: 16px;
font-family: $headings-font-family;
}

.panel-chart {
  @include panel-variant(#ddd, #fff, black, #01AF9A);
}

.panel-chart {
  @include panel-variant(#ddd, #fff, #01AF9A, #01AF9A);
}

.panel-purple {
  @include panel-variant(#ddd, #fff, $purple, $purple);
}

.panel-lime {
  @include panel-variant(#ddd, #fff, $lime, $lime);
}



.section {
  @include make-row();

}

.validation-summary {
  padding: 10px;
  
}

.validation-summary.has-error {
    color: $brand-danger;
    background-color: $state-danger-bg;
    border-left: 5px solid darken($state-danger-bg, 8%);
  }


.has-error .validation-message {
  color: $state-danger-text;
}
  
.has-error .inline-message {
  color: $state-danger-text;
  background-color: $state-danger-bg;
}

.has-success .inline-message {
  color: $state-success-text;
    background-color: $state-success-bg;
}

.global-message {
  display: inline-block;
  text-align: center;
  border-width: 1px;
  border-style: solid;
  padding: 3px 9px;
  max-width: 200px;
  clear: both;
  border-radius: 3px;
  
  
  &.success {
    color: $state-success-text;
    background-color: $state-success-bg;
      border-color: $state-success-border;
  }
   &.error {
    color: $state-danger-text;
    background-color: $state-danger-bg;
    border-color: $state-danger-border;
  }
    &.processing {
    color: $state-warning-text;
    background-color: $state-warning-bg;
    border-color: $state-warning-border;
  }
}




.btn-primary-alt {
  @include button-variant($brand-primary, rgba(0,0,0,0), $brand-primary);
  border-width: 2px;
}

.btn-warning-alt {
  @include button-variant($brand-warning, rgba(0,0,0,0), $brand-warning);
  border-width: 2px;
}




.modal-footer {
text-align: left;
}
