
//Panel Overrides 

.panel-title {
  font-family: $headings-font-family;
  font-weight: $panel-title-weight;
  text-transform: uppercase;
  font-size: 16px;
  padding: 3px 0px 1px 2px;
}

//Center aligns ad-hoc elements in a panel heading
.panel-heading {
    label {
      padding-top: 13px;
    }
  select {
    margin-top:7px;
  }
}

.panel {
  margin-bottom: 23px;

  &.no-border {
    border: none;
    box-shadow: none;
  }
}

.panel-blank, .panel {
  border: 1px solid #ddd;
  .panel-title {
    color: #777;
  }
}

.panel-default {
  border: 1px solid #ddd;
  box-shadow: none;
  .panel-heading {color:$text-muted;}
}

//Accented Panel Definition

@mixin panel-accent-color($color) {
  border:none;
  border-top: 1px solid $color;
  .panel-heading {
    border-top: 3px solid $color;
    .panel-title {color: $text-muted;}
  }
}

.panel-primary-accent {
  @include panel-accent-color($primary);
}

.panel-blue-accent {
  @include panel-accent-color($blue);
}

.panel-green-accent {
  @include panel-accent-color($green);
}

.panel-purple-accent {
  @include panel-accent-color($purple);
}


//Panel coloring Overrides ..ToDo move to bootstrap variables

.panel-primary .panel-heading {
  color: #FFF;
  border: none;
}

.panel-success .panel-heading {
  background-color: $green;
  color: white;
  border: none;
}

.panel-info .panel-heading {
  background-color: $blue;
  color: white;
  border: none;
}


//Panel with Controls //

.with-controls {

  @extend .clearfix;
   .modal-title, .panel-title {
	font-weight: 300;
    display:inline-block;
    float: left;
	}

  .btn-default {
	  
	  background: rgba(0,0,0,0);
	  border: 1px solid rgba(0,0,0,.1);
 }
}



//Panels with Tabs
.panel-dark .panel-nav {
  background: #697D8B;
  
  &.nav-pills a {
    @include border-radius(0px);
    &:hover {
      background: rgba(0,0,0,.2);
      color: white;
    }
  }
  
  > li.active a {
    background: #86A4AD;
  }
}

.panel-default .panel-nav {
  background: #eee;
  
  &.nav-pills a {
    @include border-radius(0px);
    color: #333;
    &:hover {
      background: rgba(0,0,0,.2);
    }
  }
  
  > li.active a {
    background: #fafafa;
    
  }
}

//Add to make panel appear clickable

.panel-clickable:hover {
  cursor: pointer;
  background-color: #fafafa;
  box-shadow: 0px 2px 2px rgba(0,0,0,.2);
  @include transition(.5s ease);
}





.panel-heading.with-icon-buttons {
padding: 0px;
@extend .clearfix;
 .panel-title {float: left; display: inline-block; padding: 10px 15px;}


  a.icon-btn {
	  padding: 11px 13px;
	  display:inline-block;
      color: #777;
	  &:hover {
		  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
		  background: rgba(255,255,255,.05);
		  cursor: pointer;
	  }
	  &:active {
		 box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.14);
		 background: rgba(0,0,0,0); 
	  }
  }


}

.chart-panel { 

	.panel-body {
	background-color: $dominion;
	border-bottom: none;
	border-radius: 5px 5px 0px 0px;
	
	color: white;
	}
	.table {
		border-top: none !important;
	}
}



.panel-heading.with-icons {
  @extend .clearfix;
  .panel-title {
    display: inline-block;
    float:left;
  }
  
}
.panel-icons {
  float: right;
  display: inline-block;
  
}

.modal-filter {
background-color: #f7f7f7;
padding: 8px;
border-bottom: 1px solid #ddd;
@extend .clearfix;
}

.panel-dark {
  @include panel-variant(#ddd, #fff, #2F525E, #2F525E);
}

.panel-green {
  @include panel-variant(#ddd, #ffffff, $brand-success, $brand-success);
}




