.panel-body {
    h3 {
        color: #999;
        font-weight: $headings-font-weight-medium;
        font-size: 16px;
        margin-top: 0px;
        margin-bottom: 1em;
    }
}


$panel-list-item-padding: ($panel-body-padding + 5);

// used to display a list of object information within a panel
.panel-list {
    
    // remove the panel-body padding
    margin: ($panel-body-padding * -1);

    .panel-list-item {
        padding: $panel-list-item-padding;
        border-bottom: 1px solid lighten($panel-default-border, 3%);

        &:last-child{
            border-bottom: none;
        }

        &.disabled {
            background-color: #F8F8F8;
            color: #999;
        }

        .heading {
            margin-bottom: 1em;
        }

        .panel-footer {
            margin-bottom: ($panel-list-item-padding * -1);
            margin-left: ($panel-list-item-padding * -1);
            margin-right: ($panel-list-item-padding * -1);
            background-color: #FFF;
            border-top: none;
        }
    }

}
