.folder {
    text-align: center;
    height: 136px;

    .folder__graphic {
        height: 80px;
        background-image: url('../../webcore/Images/Resources/Folder.png');
        background-position: center top;
        background-repeat: no-repeat;
        background-size: 94px;
    }

    .badge-sm {
        padding: 1em;
        line-height: 0em;
        position: relative;
        top: 50%;
        left: 50%;
        -ms-transform: translate(-50%,-50%);
        -webkit-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
        float: left;
    }

    h2 {
        font-weight: bold;
        font-size: 14px;
        margin-top: 0;
        }
}

.resource {
    height: 50px;
    padding-left: 50px;
    margin-bottom: 30px;
    background-position: left top;
    background-repeat: no-repeat;
    background-size: contain;
    

    &.resource--doc {
        background-image: url('../../webcore/Images/Resources/DOC.png');
    }
    &.resource--docx {
        background-image: url('../../webcore/Images/Resources/DOCX.png');
    }
    &.resource--xls {
        background-image: url('../../webcore/Images/Resources/XLS.png');
    }
    &.resource--xlsx {
        background-image: url('../../webcore/Images/Resources/XLSX.png');
    }
    &.resource--pdf {
        background-image: url('../../webcore/Images/Resources/PDF.png');
    }
    &.resource--rtf {
        background-image: url('../../webcore/Images/Resources/RTF.png');
    }
    &.resource--txt {
        background-image: url('../../webcore/Images/Resources/TXT.png');
    }
    &.resource--link {
        background-image: url('../../webcore/Images/Resources/LINK.png');
    }
    &.resource--vid {
        background-image: url('../../webcore/Images/Resources/VID.png');
    }
    h3 {
        margin: 5px 0;
        font-size: 14px;
        font-weight: normal;
        display: inline-block;
        clear: right;
    }
    .fa {
        padding-right: 3px;
        display: inline-block;
        span {
            font-family: "Roboto", Arial, sans-serif;
            margin-left: 5px;
        }

        &:last-child {
            padding-left: 6px;
            padding-right: 0;
            border-left: 1px solid #777;
        }
    }
}


