$chip-border-radius: 2px;
$chip-action-badge-border-radius: 0px $chip-border-radius $chip-border-radius 0px;
$chip-padding-horizontal: 10px;
$chip-padding-vertical: 10px;

$chip-padding-horizontal-sm: 5px;
$chip-padding-vertical-sm: 3px;

$chip-default-color:  $btn-default-color;
$chip-default-bg:     $btn-default-bg;
$chip-default-border: $btn-default-border;
$chip-default-action: darken($chip-default-bg, 8%);

$chip-primary-color:  $btn-primary-color;
$chip-primary-bg:     $btn-primary-bg;
$chip-primary-border: $btn-primary-border;
$chip-primary-action: darken($chip-primary-bg, 8%);

$chip-secondary-color:  $btn-secondary-color;
$chip-secondary-bg:     $btn-secondary-bg;
$chip-secondary-border: $btn-secondary-border;
$chip-secondary-action: lighten($chip-secondary-bg, 8%);

$chip-disabled-color:  #eee; 
$chip-disabled-bg:     #B3B3B3; 
$chip-disabled-border: $chip-disabled-bg;
$chip-disabled-action: darken($chip-disabled-bg, 8%);

$chip-gray-color:  #eee; 
$chip-gray-bg:     darken(#B3B3B3, 30%); 
$chip-gray-border: $chip-gray-bg;
$chip-gray-action: darken($chip-gray-bg, 8%);

$chip-blue-color:  #fff;
$chip-blue-bg:     #418BCD;
$chip-blue-border: #418BCD;
$chip-blue-action: darken($chip-blue-bg, 8%); 

$chip-content-font-size: 13px;
$chip-action-font-size: 13px;
$chip-action-icon-font-size: 16px;

@mixin chip-variant($color, $background, $border, $action) {
    
    @include button-variant($color, $background, $border);

    .chip-action {
        color: $color;
        background-color: $background;

        &.action-block {
            background-color: $action;
        }
    }
}

@mixin chip-size-variant($padding-vertical, $padding-horizontal) {
    padding: $padding-vertical $padding-horizontal;
    margin-bottom: ceil($padding-vertical / 2);

    .chip-content {
        font-size: $chip-content-font-size;
    }

    .chip-action {
        $action-vertical-padding: (($padding-vertical * 2 + $chip-content-font-size) - $chip-action-font-size) / 2;
        $action-vertical-padding-ceil: ceil($action-vertical-padding);
        $action-vertical-padding-floor: floor($action-vertical-padding);

        $action-icon-vertical-padding: (($padding-vertical * 2 + $chip-content-font-size) - $chip-action-icon-font-size) / 2;
        $action-icon-vertical-padding-ceil: ceil($action-icon-vertical-padding);
        $action-icon-vertical-padding-floor: floor($action-icon-vertical-padding) - 1;
            
        top: 0px;
        right: 0px;
        font-size: $chip-action-font-size;
        min-width: $chip-action-icon-font-size + $padding-horizontal * 2;
        padding: $action-vertical-padding-ceil $padding-horizontal $action-vertical-padding-floor $padding-horizontal;

        &.with-icon {
            font-size: $chip-action-icon-font-size;
            padding: $action-icon-vertical-padding-ceil $padding-horizontal $action-icon-vertical-padding-floor $padding-horizontal;
        }
	
    }
}

.chip {
    display: block;
    position: relative;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.07);
    cursor: pointer;
    vertical-align: middle;
    border: 1px solid transparent;
    @include border-radius($chip-border-radius);
    @include chip-size-variant($chip-padding-vertical, $chip-padding-horizontal);

    &:hover {
        box-shadow: 0px 2px 5px rgba(0, 0, 0,.2);
    }

    &:active {
        box-shadow: 0px 2px 1px rgba(0,0,0,.1), inset 0 2px 5px rgba(0,0,0,.05);
    }

    &.chip-sm {
        @include chip-size-variant($chip-padding-vertical-sm, $chip-padding-horizontal-sm);
    }

    &.disabled,
    &[disabled],
    fieldset[disabled] & {
        cursor: not-allowed;
        @include opacity(.65);
        @include box-shadow(none);
    }

    .chip-content {
        vertical-align: middle;
        width: 85%;
        @include text-overflow();
    }

    .chip-action {
        position: absolute;
        z-index: 2;    
        text-align: center;
        font-weight: 600;
        @include border-radius($chip-action-badge-border-radius);

        &.with-icons {
            font-weight: normal;
        }
    }
}

.chip-primary {
    @include chip-variant($chip-primary-color, $chip-primary-bg, $chip-primary-border, $chip-primary-action);
}

.chip-primary-dark {
    @include chip-variant($chip-primary-color, #00A58F, #00A58F, #00917D);
}

.chip-default {
    @include chip-variant($chip-default-color, $chip-default-bg, $chip-default-border, $chip-default-action);
}

.chip-secondary {
    @include chip-variant($chip-secondary-color, $chip-secondary-bg, $chip-secondary-border, $chip-secondary-action);
}

.chip-blue {
    @include chip-variant($chip-blue-color, $chip-blue-bg, $chip-blue-border, $chip-blue-action);
}

.chip-gray {
    @include chip-variant($chip-gray-color, $chip-gray-bg, $chip-gray-border, $chip-gray-action);
}

.chip-disabled {
    @include chip-variant($chip-disabled-color, $chip-disabled-bg, $chip-disabled-border, $chip-disabled-action);

    &,
    &.disabled,
    &[disabled],
    fieldset[disabled] & {
        //@include opacity(.85);
    }
}

