.add-company {
    width: 140px !important;
    background-color: #a1c5e4;
    height: 130px;
    border-radius: 5px;
    border: 1px solid #2E68B1;
    text-align: center;
    color: #2E68B1;
    margin: 0px 15px 15px 0px;
}

.add-client {
    background-color: #80e1d9;
    border: 1px solid #4dd4ca;
    color: #00a595;
    font-weight: bold;
}

.plus-icon {
    font-size: 30px !important;
    color: #4dd4ca;
    text-align: center;
    display: inherit;
    margin-top: 38px;
    opacity: 0.8;
}

.tile-box {
    width: 140px;
    height: 130px;
    border-radius: 5px;
    text-align: center;
    margin: 0px 15px 15px 0px;
    color: #fff;
    display: inline-block;
}

.tile-box > p > span {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-height: 16px;
    /* max-height: 50px; */
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    width: auto;
    height: 30px;
    text-align: center;
}

.tile-box:hover {
    opacity: .8;
}

.blue-box {
    background-color: #458ccb;
    border: 1px solid #2E68B1;
    color: #fff;
}

.green-box {
    background-color: #00c4b4;
    color: #fff;
    border: 1px solid #00c4b4;
}

.green-box > span {
    color: #fff;
}

.tile-box > h5 {
    margin: 0px;
    padding: 8px 5px;
    background: #3771a5;
    font-size: 12px;
}

.green-box > h5 {
    background: #00a595;
    min-width: 135px;
    height: 27px;
}

.building-icon {
    font-size: 30px !important;
    color: #fdfdfd;
    text-align: center;
    display: inherit;
    margin-top: 15px;
    opacity: 0.6;
}
/*.tile-box:first-child {
            margin: 0px 15px 15px 15px;
        }*/

.mandatory {
    color: #FF6200;
    font-size: 18px;
    font-weight: bold;
    vertical-align: top;
}

.form-control .col-md-6 > input {
    min-width: 64%;
}



@media(max-width:767px) {
    .Save-Organizational {
        margin: 15px 0px 0px 0px !important;
    }

    .tile-box {
        margin: 10px 0px 0px 0px !important;
    }
    /*.tile-box:first-child {
                    margin: 15px 0px 0px 0px !important;
                }*/
    .tab-content > .active {
        padding: 20px;
    }
}

.main-label > label {
    font-weight: bold;
    color: #555;
    display: inline-block;
    padding: 5px 0px 3px 0px;
    font-size:13px;
    margin:0px;
}

.main-container .row {
    margin: 10px -15px;
}

h4.orginazation-title {
    text-align: center;
    text-transform: uppercase;
    color: #555;
    padding-top: 7px;
}

@media screen and (min-width: 768px) {
    .modal-dialog.new-modal-dialog {
        width: 60%;
        margin: 30px auto;
    }
}

.tile-model-box {
    width: 120px;
    height: 125px;
}

.tile-model-box > h5 {
    min-width: auto;
}

.text-center {
    text-align: center;
}

.new-modal-dialog > .modal-content > .modal-body {
    height: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.new-modal-dialog > .modal-content > .modal-footer {
    margin-top: 0px;
}

.Save-Organizational {
    margin: 0px 15px !important;
    /*padding: 7px 11px !important;*/
}
/* Landscape tablets and medium desktops */

@media (min-width: 0px) and (max-width: 991px) {
    .Save-Organizational {
        margin: 15px 0px !important;
        /*padding: 7px 11px !important*/;
    }

    .new-modal-dialog > .modal-content > .modal-body {
        padding: 20px 10px;
    }
}

.main-modal {
    overflow: hidden;
}

.required {
    color: #FF6200;
    font-size: 18px;
    font-weight: bold;
    vertical-align: middle;
}

.non-selected-box {
    opacity: 0.6;
}

.Clearfix {
    clear: both;
}

.searchbox {
    float: right;
    width: auto;
    min-width: 280px;
    padding: 0px 12px;
    margin: 0px 10px;
}
