@mixin widget-color($color, $text-color) {
 background: $color;
 .widget-label {color:$text-color; }
 .panel-heading {border-bottom-color: $text-color;}
 hr {border-top-color: $text-color;}
 .big-num  {color:white;}
}

.widget {
  line-height: 1em;
  border:none;
  color:white;
}


.widget hr { 
margin-bottom: 16px;
margin-top: 10px;
}
.big-num {
  font-size: 40px;
  line-height: 1em;
  font-weight: 200;
}

.big-num-label {
  font-size: 12px;
  padding-left: 5px;
  font-family: arial;
  font-style: italic;
  text-transform: uppercase;
}

.little-num {
  font-size: 14px;
  line-height: 1em;
  font-weight: 200;
}

.widget-title {
color: white;
font-size: 18px;
}
.widget-label {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: normal;
  padding-bottom:3px;
}

.widget-icon {
  color: white;
  font-size: 54px;
  float: left;
  width: 65px;
  padding-top: 2px;
  height: 60px;
}



.widget-icon .fa-star-half-o {

  font-size: 58px;
}


.chart-panel { 
  .panel-body {
  background-color: $dominion;
  border-bottom: none;
  border-radius: 5px 5px 0px 0px;
  color: white;
}
  .table {
    border-top: none !important;
  }
}


//Specific Widget Colors

.widget-blue {
  @include widget-color($blue, #B0CBEC);
}
.widget-green {
  @include widget-color($green, #B9E8C0);
}

.widget-primary {
  @include widget-color($primary, #C6E8E0);
}