.form-row {
    @include make-row();

    border-top: 1px solid lighten($panel-default-border, 5%);

    &:hover {        
        background-color: #f8f8f8;
    }

    .form-group, .form-group-inline {
        margin-top: 7px;
        margin-bottom: 7px;
    }

    .panel-list .panel-list-item & {
        @include make-row($panel-list-item-padding * 2);
    }
}

.form-group-wrapper {

    margin-left: $grid-gutter-width / 2;
    margin-right: $grid-gutter-width / 2;

    &.gutter-sm {
        margin-left: $grid-gutter-width / 4;
        margin-right: $grid-gutter-width / 4;
    }

    .form-inline & {
        display: inline-block;
    }
    
}

.form-control-separator {
    @extend .form-control-static;
    @extend .text-muted;

    margin-left: 3px;
    margin-right: 3px;
    font-size: 1.1em;
}

.form-control-layover {
    @extend .form-control-feedback;
    top: 0px;
    right: $input-height-base / 2;
}