.table-selectable {
  tbody > tr > td {
  background: $lightest-gray;
  border-bottom: 2px solid white;
  }
  .selected td {
  background: #CBF0ED;
  color: #333;
  }

  tbody > tr:hover  > td {
    cursor: pointer;
    background:#eee;
  }
  tbody > tr.selected:hover  > td {
	cursor: pointer;
    background: darken(#CBF0ED, 8%);
  }

}

.modal.info-dialog {
    .modal-dialog {

        margin-top: 200px;

        .modal-header {
            border-top-left-radius: $border-radius-large;
            border-top-right-radius: $border-radius-large;
            background-color: $brand-primary;
            color: #fff;
        }

        .modal-body {
            
            @extend .text-muted;

            .info-dialog-icon {
                text-align: center;

                i.fa {
                    vertical-align: top;
                    font-size: 6em;
                }
            }

            .info-dialog-text {

                // from .lead
                margin-bottom: 20px;
                font-size: 16px;
                font-weight: 300;
                line-height: 1.4;
            }
        }
        
    }
}